.consulting {
    min-height: 680px;
    background: url(../img/bg/consult_bg.jpg) center center/cover no-repeat;
    padding: 100px 0;
    &__descr {
        font-weight: 300;
        font-size: 20px;
        line-height: 30px;
        color: #FFF;
        margin-top: 8px;
        margin-bottom: 36px;
    }
    .error {
        border: 1px solid $red;
    }
    label.error {
        border: none;
        text-align: center;
        font-size: 15px;
        color: #fff;
    }
}